@font-face {
    font-family: "Nunito Sans";
    src: url("./fonts/NunitoSans-Bold.eot");
    src: local("./fonts/Nunito Sana Bold"), local("NunitoSans-Bold"),
        url("./fonts/NunitoSans-Bold.eot?#iefix") format("embedded-opentype"),
        url("./fonts/NunitoSans-Bold.woff") format("woff"), url("./fonts/NunitoSans-Bold.ttf") format("truetype");
    font-weight: 700;
    font-style: normal;
}

@font-face {
    font-family: "Nunito Sans";
    src: url("./fonts/NunitoSans-Black.eot");
    src: local("./fonts/Nunito Sana Black"), local("NunitoSans-Black"),
        url("./fonts/NunitoSans-Black.eot?#iefix") format("embedded-opentype"),
        url("./fonts/NunitoSans-Black.woff") format("woff"), url("./fonts/NunitoSans-Black.ttf") format("truetype");
    font-weight: 900;
    font-style: normal;
}

@font-face {
    font-family: "Helvetica";
    src: url("./fonts/HelveticaRegular.eot");
    src: local("./fonts/Helvetica Regular"), local("Helvetica-Regular"),
        url("./fonts/HelveticaRegular.eot?#iefix") format("embedded-opentype"),
        url("./fonts/HelveticaRegular.woff") format("woff"), url("./fonts/HelveticaRegular.ttf") format("truetype");
    font-weight: 400;
    font-style: normal;
}

